.calculator-page {
  display: flex;
  width: 80%;
  padding-block: 50px;
  justify-content: space-around;
  align-items: flex-start;
}

.calculator-display {
  background-color: #858693;
  padding: 20px;
  text-align: right;
}

.calculator-keypads {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.calculator-keypads > * {
  padding: 20px;
  background-color: #e0e0e0;
  cursor: pointer;
}

.key17 {
  grid-column: span 2;
}

.key4,
.key8,
.key12,
.key16,
.key19 {
  background-color: #ed913d;
}
